import * as React from 'react'
import { graphql } from 'gatsby'

const HomePage = () => {
const HomePage = ({data}) => {
  return (
    <div>
     Hello!
     {data.site.siteMetadata.description}
    </div>
  )
}
}
export const query = graphql`
  query HomePageQuery {
    site {
      siteMetadata {
        description
      }
    }
  }
`

export default HomePage